@media (min-width: 768px) {
    .mobile-header {
        display: none;

        .arrow-back {
            cursor: pointer;
        }
    }

    .event-details {
        margin: 0 auto;
        padding-top: 32px;
        width: 900px;

        &.registered {
            background-color: #fff1e8
        }

        .event-details-thumbnail {
            display: inline-block;
            max-width: 400px;
            vertical-align: top;
            width: 40%;
        }

        .event-details-wrapper {
            display: inline-block;
            width: 60%;
            padding: 16px;

            &.registered {
                color: #a65523;
            }

            .event-details-title {
                font-size: 1.5rem;
                font-weight: 700;
                padding: 0 16px;

                .event-details-registered {
                    margin-bottom: 8px;
                    display: block;
                }

            }

            .event-details-desc {
                font-size: 1rem;
                font-weight: 400;
                padding: 0 16px;

                .title {
                    font-size: 1rem;
                    color: #000;
                }

                .event-details-link {
                    text-decoration: underline;
                    padding: 0;
                    cursor: pointer;
                }

            }

            .event-details-link {
                text-decoration: underline;
                padding: 0 16px;
                cursor: pointer;
            }

            .event-details-button {
                display: flex;
                padding: 20px 16px;
                cursor: pointer;
            }
        }

        .event-register-button {
            display: inline-block;
            padding: 0 16px;
            width: 150px;
        }

        .event-details-cards {
            margin: 0 16px 10px;
            margin: 16px 0px 16px;
            background-color: white;
            border-radius: 25px;
            padding: 24px 16px;

            .title {
                font-size: 1.3rem;
                font-weight: 400;
            }

            .item {
                padding-top: 16px;
                vertical-align: top;

                .icon {
                    display: inline-block;
                    padding-top: 5px;
                    vertical-align: top;
                    width: 30px;
                }

                .info {
                    color: #2c2c2c;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;

                    &.warning {
                        color: red;
                    }
                }

                .warning {
                    color: red;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;
                }


            }
        }


    }

    .event-media-list {
        margin-bottom: 50px;
        padding: 32px 0 16px;

        .title {
            margin-bottom: 8px;
            padding-left: 16px;
            color: black;
            font-size: 1rem;
            font-weight: 700;
        }

        .media-list {
            height: 270px;
            overflow-x: scroll;
            //padding: 10px 0;
            position: relative;
            white-space: nowrap;
            width: 100%;
            scrollbar-width: none;
            -ms-overflow-style: none;


        }

        .media-list-item {
            background-color: white;
            border-radius: 12px;
            cursor: pointer;
            display: inline-flex;
            flex-direction: column;
            height: 240px;
            margin-right: 8px;
            overflow: hidden;
            -webkit-user-select: none;
            user-select: none;
            width: 284px;

            .media-list-item:first-child {
                margin-left: 16px;
            }

            .media-list-item:last-child {
                margin-right: 16px;
            }

        }

        .media-list-thumb {
            position: relative;
            width: 100%;

            .play-icon {
                position: absolute;
                right: 8px;
                top: 8px;
                text-align: center;
                background: white;
                border-radius: 50%;
                width: 24px;
                height: 24px;
            }
        }

        .media-list-item-title {
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            color: black;
            display: -webkit-box !important;
            font-size: 1rem;
            font-weight: 400;
            margin: 16px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: normal;

        }

    }

    .event-register {
        display: none;
    }

    .amway-button {
        background-color: black;
        border: 1px solid black;
        border-radius: 100px;
        color: white;
        cursor: pointer;
        font-size: .875rem;
        font-weight: 400;
        height: 100%;
        line-height: 30px;
        position: relative;
        text-align: center;
        width: 100%;
        display: inline-block;
    }

    .popEvent{
        .body_container{
            height: 680px;
        }
    }

}


